import React from 'react';

class LaterBox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dt: this.props.dt,
            weather: this.props.weather
        };
    }

    getTime = (dt) => {
        let date = new Date(dt * 1000);
        let hour = date.getHours();
        let minute = date.getMinutes();
        if (hour < 10) { hour = '0' + hour; }
        if (minute < 10) { minute = '0' + minute; }
        let res = {
            hour: hour,
            minute: minute
        }
        return res;
    }

    render() {
        const {dt, weather} = this.state;

        return (
            <div className="currentWrap wrapTop">
                <h1 className='todayString'>Позднее</h1>
                <div className="laterBoxes">
                    <div className="laterBox">
                        <h3 className='laterTemp'>{Math.round(weather[1].temp)}</h3>
                        <h3 className='laterTime'>{this.getTime(weather[1].dt).hour}:{this.getTime(weather[1].dt).minute}</h3>
                    </div>
                    <div className="laterBox">
                        <h3 className='laterTemp'>{Math.round(weather[6].temp)}</h3>
                        <h3 className='laterTime'>{this.getTime(weather[6].dt).hour}:{this.getTime(weather[6].dt).minute}</h3>
                    </div>
                    <div className="laterBox">
                        <h3 className='laterTemp'>{Math.round(weather[12].temp)}</h3>
                        <h3 className='laterTime'>{this.getTime(weather[12].dt).hour}:{this.getTime(weather[12].dt).minute}</h3>
                    </div>
                </div>
            </div>
        );
    }
}

export default LaterBox;